import React, { CSSProperties } from 'react';
import { IProps, HTMLDivProps } from '@uiw/utils';
import { Check } from '@uiw/icons/lib/Check';
import { Close } from '@uiw/icons/lib/Close';

import {
  StepsStyleItem,
  StepsStyleItemTail,
  StepsStyleItemTailI,
  StepsStyleItemHead,
  StepsStyleItemHeadInner,
  StepsStyleItemMain,
  StepsStyleItemMainTitle,
  StepsStyleItemMainDescription,
  StepsStyleItemHeadInnerDot,
  StepsStyleItemHeadInnerIcon,
  StepsStyleItemHeadInnerSvg,
} from './style';
export interface StepProps extends IProps, Omit<HTMLDivProps, 'title'> {
  nextError?: boolean | undefined;
  title?: React.ReactNode;
  description?: React.ReactNode;
  status?: 'wait' | 'process' | 'finish' | 'error' | 'success';
  progressDot?: boolean;
  itemWidth?: number;
  stepNumber?: string;
  adjustMarginRight?: number;
  icon?: React.ReactNode;
  direction?: 'horizontal' | 'vertical';
}

export default function Step(props: StepProps) {
  const {
    prefixCls = 'w-steps',
    className,
    style,
    status,
    itemWidth,
    icon,
    adjustMarginRight,
    stepNumber,
    title,
    description,
    progressDot,
    direction,
    ...restProps
  } = props;
  const classString = [
    `${prefixCls}-item`,
    `${prefixCls}-item-${status}`,
    className,
    icon ? `${prefixCls}-custom` : null,
  ]
    .filter(Boolean)
    .join(' ')
    .trim();
  const stepItemStyle: CSSProperties = { ...style };
  const stepItemDotStyle: CSSProperties = {};
  if (itemWidth) {
    stepItemStyle.width = itemWidth;
  }
  if (adjustMarginRight) {
    stepItemStyle.marginRight = adjustMarginRight;
    if (progressDot) {
      stepItemDotStyle.paddingRight = Math.abs(adjustMarginRight);
    }
  }
  let iconNode = null;
  if (progressDot) {
    iconNode = <StepsStyleItemHeadInnerDot params={{ status }} className={`${prefixCls}-dot`} />;
  } else if (icon && typeof icon !== 'string') {
    iconNode = (
      <StepsStyleItemHeadInnerIcon params={{ status, icon: !!icon }} className={`${prefixCls}-icon`}>
        {icon}
      </StepsStyleItemHeadInnerIcon>
    );
  } else if (status === 'finish' || status === 'error') {
    iconNode = <StepsStyleItemHeadInnerSvg as={status === 'finish' ? Check : Close} />;
  } else {
    iconNode = (
      <StepsStyleItemHeadInnerIcon params={{ status, icon: !!icon }} className={`${prefixCls}-icon`}>
        {stepNumber}
      </StepsStyleItemHeadInnerIcon>
    );
  }
  return (
    <StepsStyleItem
      {...restProps}
      params={{
        status,
        direction,
      }}
      className={classString}
      style={stepItemStyle}
    >
      <StepsStyleItemTail
        className={`${prefixCls}-item-tail`}
        params={{ direction, dot: !!progressDot, status: status }}
        style={stepItemDotStyle}
      >
        <StepsStyleItemTailI
          params={{
            direction,
            dot: !!progressDot,
            status: status,
            nextError: props.nextError,
          }}
          style={{ paddingRight: '100%' }}
        />
      </StepsStyleItemTail>
      <StepsStyleItemHead params={{ dot: !!progressDot }} className={`${prefixCls}-item-head`}>
        <StepsStyleItemHeadInner
          params={{
            direction,
            dot: !!progressDot,
            status: status,
            icon: !!icon,
          }}
          className={[`${prefixCls}-item-inner`, !!icon && 'is-icon'].filter(Boolean).join(' ').trim()}
        >
          {iconNode}
        </StepsStyleItemHeadInner>
      </StepsStyleItemHead>
      <StepsStyleItemMain
        params={{
          direction,
          dot: !!progressDot,
        }}
        className={`${prefixCls}-item-main`}
      >
        <StepsStyleItemMainTitle
          params={{
            dot: !!progressDot,
            status: status,
          }}
          className={`${prefixCls}-item-title`}
        >
          {title}
        </StepsStyleItemMainTitle>
        {description && (
          <StepsStyleItemMainDescription
            params={{
              direction,
              dot: !!progressDot,
              status: status,
            }}
            className={`${prefixCls}-item-description`}
          >
            {description}
          </StepsStyleItemMainDescription>
        )}
      </StepsStyleItemMain>
    </StepsStyleItem>
  );
}
