import React from 'react';
import { IProps, HTMLButtonProps } from '@uiw/utils';
import { ButtonStyleWarp, ButtonStyleBaseLight } from './style';
import { IconStyleBase } from '@uiw/react-icon';
export * from './style';
export type ButtonType = 'primary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' | 'link';
export type ButtonSize = 'large' | 'default' | 'small';
export interface ButtonProps extends IProps, Omit<HTMLButtonProps, 'size'> {
  basic?: boolean;
  disabled?: boolean;
  active?: boolean;
  loading?: boolean;
  block?: boolean;
  icon?: React.ReactNode;
  focus?: boolean;
  /**
   * 
   * @mytag {@link link }
   * @beta
   */
  type?: ButtonType;
  /**
   * 
   * @defaultValue 'large' | 'default' | 'small'
   */
  size?: ButtonSize;
  htmlType?: 'button' | 'submit' | 'reset';
  onClick?: (e: React.MouseEvent<HTMLButtonElement> & MouseEvent) => void;
}

/**@internal */
const Button = React.forwardRef<HTMLButtonElement, ButtonProps>((props, ref) => {
  const {
    prefixCls = 'w-btn',
    disabled = false,
    active = false,
    loading = false,
    block = false,
    basic = false,
    htmlType = 'button',
    type = 'light',
    size = 'default',
    icon,
    className,
    children,
    ...others
  } = props;

  const cls = [
    className,
    prefixCls,
    size ? `${prefixCls}-size-${size}` : null,
    type ? `${prefixCls}-${type}` : null,
    basic ? `${prefixCls}-basic` : null,
    loading ? `${prefixCls}-loading` : null,
    disabled || loading ? 'disabled' : null,
    active ? 'active' : null,
    block ? 'block' : null,
  ]
    .filter(Boolean)
    .join(' ')
    .trim();

  return (
    <ButtonStyleWarp
      {...others}
      as={type === 'light' ? ButtonStyleBaseLight : 'button'}
      ref={ref}
      type={htmlType}
      disabled={disabled || loading}
      className={cls}
      param={{
        focus: !!props.focus,
        type,
        size,
        basic,
        loading,
        disabled: disabled || loading,
        active,
        block,
      }}
    >
      {icon ? <IconStyleBase>{icon}</IconStyleBase> : <React.Fragment />}
      {children &&
        React.Children.map(children, (child: React.ReactNode) => {
          if (!child) return child;
          if (React.isValidElement(child)) return child;
          return <span>{child}</span>;
        })}
    </ButtonStyleWarp>
  );
});

export default Button;
