import { useState } from 'react'
import { ProTable, useTable } from '@uiw-admin/components'
import { useCity } from '@/servers'

export default function Demo() {
  const table = useTable('/api/getData', {
    // {total , data: }
    formatData: (data) => {
      return {
        total: data.total,
        data: data.data,
      }
    },
    //  pageIndex   searchValues 
    query: (pageIndex, pageSize, searchValues) => {
      return {
        page: pageIndex,
        pageSize,
        data: searchValues,
      }
    },
  })

  const [val, setVal] = useState('')

  const { city = [], isLoading } = useCity(val)

  const handleSearch = (val: string) => {
    setVal(val)
  }
  return (
    <ProTable
      // 
      operateButtons={[
        { label: '', type: 'primary' },
        {
          label: '',
          onClick: () => {
            table.selection.unSelectAll()
          },
        },
      ]}
      // 
      searchBtns={[
        {
          label: '',
          type: 'primary',
          onClick: () => {
            table.onSearch()
          },
        },
        {
          label: '',
          onClick: () => {
            table.onReset()
          },
        },
      ]}
      table={table}
      onBeforeSearch={({ current }) => {
        const errorObj: any = {}
        if (!current.name) errorObj.name = ''
        if (Object.keys(errorObj).length > 0) {
          const err: any = new Error()
          err.filed = errorObj
          throw err
        }
        return true
      }}
      rowSelection={{
        //  checkbox radio
        type: 'checkbox',
        //  columnkey
        selectKey: 'name',
        // 
        defaultSelected: [''],
      }}
      // 
      onPageChange={() => {
        table.selection.unSelectAll()
      }}
      paginationProps={{
        pageSizeOptions: [10, 20, 30],
        pageSize: 10,
        onShowSizeChange: (current, pageSize) => {
          console.log(current, pageSize)
        },
      }}
      columns={[
        {
          title: '',
          key: 'name',
          props: {
            widget: 'input',
            initialValue: 'zzz',
            key: 'name',
            // 
            widgetProps: {
              preIcon: 'user',
              placeholder: '',
            },
          },
        },
        {
          title: '',
          key: 'age',
          width: 50,
          props: {
            widget: 'select',
            option: [
              { label: '20', value: 20 },
              { label: '10', value: 10 },
            ],
          },
        },
        {
          title: '',
          key: 'info',
          props: {
            label: '',
            key: 'detailAddress',
            widget: 'textarea',
          },
        },
        {
          title: 'Switch',
          key: 'Switch',
          props: {
            widget: 'switch',
          },
        },
        {
          title: 'timePicker',
          key: 'timePicker',
          props: {
            widget: 'timePicker',
          },
        },
        {
          title: 'MonthPicker',
          key: 'MonthPicker',
          props: {
            widget: 'monthPicker',
          },
        },
        {
          title: 'DateInput',
          key: 'DateInput',
          props: {
            widget: 'dateInput',
          },
        },
        {
          title: 'timePicker',
          key: 'timePicker1',
          props: {
            widget: 'timePicker',
          },
        },
        {
          title: 'Radio',
          key: 'Radio',
          props: {
            widget: 'radio',
            option: [
              { label: '', value: 'man' },
              { label: '', value: 'girl' },
            ],
          },
        },
        {
          title: '',
          key: 'city',
          width: 50,
          props: {
            widget: 'searchSelect',
            widgetProps: {
              allowClear: true,
              placeholder: '',
              showSearch: true,
              loading: isLoading,
              option: city,
              onSearch: handleSearch,
            },
          },
        },
      ]}
    />
  )
}
