import { Button, Dropdown, Menu } from 'uiw'
import { ProTable, useTable } from '@uiw-admin/components'
import Detail from './Detail'
import { Dispatch, useDispatch } from '@kkt/pro'

const Demo = () => {
  const dispatch = useDispatch<Dispatch>()

  const updateData = (payload: any) => {
    dispatch({
      type: 'demo/updateState',
      payload,
    })
  }

  const table = useTable('/api/getData', {
    // {total , data: }
    formatData: (data) => {
      return {
        total: data.total,
        data: data.data,
      }
    },
    //  pageIndex   searchValues 
    query: (pageIndex, searchValues) => {
      return {
        page: pageIndex,
        pageSize: 10,
        data: searchValues,
      }
    },
  })

  // 
  function handleEditTable(type: string, record: Record<string, any>) {
    if (type === 'add') {
      updateData({ drawerVisible: true, queryInfo: {} })
    }
    if (type === 'edit' || type === 'view') {
      dispatch({
        type: 'demo/selectById',
        payload: { id: record?.id },
      })
    }
    updateData({ isView: type === 'view', tableType: type })
  }

  const menu = (
    <div>
      <Menu bordered style={{ maxWidth: 200 }}>
        {[
          { label: '', value: 'search', onClick: () => table?.onSearch() },
          {
            label: '',
            value: 'reset',
            onClick: () => console.log(''),
          },
        ].map((item, idx) => (
          <Menu.Item onClick={item?.onClick} key={idx} text={item.label} />
        ))}
      </Menu>
    </div>
  )

  return (
    <>
      <ProTable
        searchBtns={[
          { label: '', type: 'primary', htmlType: 'submit' },
          {
            render: (
              <Dropdown menu={menu} trigger="click" placement="bottomRight">
                <Button type="danger">render</Button>
              </Dropdown>
            ),
          },
        ]}
        operateButtons={[
          {
            label: '',
            type: 'primary',
            onClick: handleEditTable.bind(this, 'add'),
          },
          {
            render: <Button type="danger">render</Button>,
          },
        ]}
        columns={[
          {
            title: '',
            key: 'name',
            props: {
              widget: 'input',
              // 
              widgetProps: {
                preIcon: 'user',
                placeholder: '',
              },
            },
          },
          {
            title: '',
            key: 'age',
            props: {
              widget: 'select',
              option: [
                { label: '20', value: 20 },
                { label: '10', value: 10 },
              ],
            },
          },
          {
            title: '',
            key: 'info',
          },
          {
            title: '',
            key: 'edit',
            width: 98,
            render: (text, key, rowData) => (
              <div
                style={{
                  alignItems: 'center',
                  justifyContent: 'center',
                  display: 'flex',
                }}>
                <Button
                  size="small"
                  type="danger"
                  onClick={handleEditTable.bind(this, 'edit', rowData)}>
                  
                </Button>
                <Button
                  size="small"
                  type="success"
                  onClick={handleEditTable.bind(this, 'view', rowData)}>
                  
                </Button>
              </div>
            ),
          },
        ]}
        table={table}
      />
      <Detail updateData={updateData} onSearch={table.onSearch} />
    </>
  )
}
export default Demo
